<template>
  <div class="mycollect">
    <!-- 导航开始 -->
    <van-nav-bar
      title="我的收藏"
     
      left-arrow
      @click-left="onClickLeft"
    
    />
    <!-- 导航结束 -->
     <div class="lxy_bjt" v-if="lxy_list.length == 0">
            <img width="150px" src="https://wap.365msmk.com/img/empty.0d284c2e.png" alt="">
      </div>
      <ul class="lxy_sc" v-else >
        <li v-for="(item,index) of lxy_list" :key="index">
          <div>
            <p>
              {{ item.title }}
            </p>
            <van-icon name="star" size="16" @click="Changeclick(item.collect_id,index)" color="red" />
          </div>
            <p>共{{ item.section_num }}课时</p>
            <p>
              <img :src="item.teachersAvatar" alt="">
              {{ item.teachers }}
            </p>
            <p><span>{{ item.sales_num }}已报名</span>
            <span>
                <img v-if="item.price>0" width="10px" src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20191HHDExgz0u1567065946.png" alt=""> 
              {{ item.price | jg}}</span></p>
        </li>
      </ul>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      lxy_list:[]
    };
  },
  watch: {},
  computed: {},
  filters:{
      jg(val){
      if(val == 0){
        return "免费"
      }else{
        return (val/100).toFixed(2)
      }
    },
  },
  methods: {
      onClickLeft(){
         this.$router.go(-1)
      },
      async  Changeclick(id,index){

      let res = await this.$http.put(`/api/app/collect/cancel/${id}/1`)
       this.lxy_list.splice(index,1)     
     }
  },
  created() {
       this.$http.get(`/api/app/collect?page=1&limit=10&type=1`).then((res)=>{
           this.lxy_list = res.data.data.list
           console.log(this.lxy_list)
       })
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
*{
  margin: 0;
  padding: 0;
  font-size: 16px;
}
.mycollect{
    width: 100%;
    height: 100%;
    background: #f0f2f5;
}
.lxy_bjt{
    margin: 20px auto;
    text-align: center;
}
.lxy_sc{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  li{
    width: 92%;
    height: 240px;
    margin-top: 10px;
    background: #fff;
    padding: 0px 5px;
    div{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
          height: 60px;
          width: 88%;
          color: #000;
        }
    }
     p:nth-of-type(1){
       height: 60px;
       color: #999;
    }
     p:nth-of-type(2){
        height: 60px;
        margin-bottom: 2px;
        display: flex;
        align-items: center;
        color: #999;
      img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
     p:nth-of-type(3){
       border-top: 1px #ccc solid;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        span:nth-of-type(1){
          color: #999;
        }
        span:nth-of-type(2){
          color:tomato;
        }
    }
    
  }
}
</style>